import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  FaBars,
  FaTimes,
  FaFacebook,
  FaTwitter,
  FaGithub
} from "react-icons/fa";
import "./App.css";
import img1 from "./images/responsive.jpg";
import img2 from "./images/react1.png";
import img3 from "./images/web-app3.png";

import img4 from "./images/img5.jpg";
import img5 from "./images/co.jpg";

import me from "./images/me.jpg";
