import React, { useState, useEffect } from "react";
import "./Admin.css";
import axios from "axios";

const Admin = () => {
  const [email, setEmail] = useState("");
  const [pass, setPass] = useState("");
  const [messages, setMessages] = useState([]);

  const [loggedAdmin, setLoggedAdmin] = useState(
    JSON.parse(window.localStorage.getItem("token"))
  );

  const [error, setError] = useState({
    error: false,
    message: ""
  });
  const markAsRead = (id, read) => {
    setError({
      error: false,
      message: ""
    });
    axios
      .patch(`/api/admin/messages/${id}`, {
        read: !read
      })
      .then(response => {
        setMessages(response.data);
      })
      .catch(err => {
        setError({
          error: true,
          message: "Error in the server"
        });
      });
  };
  const messagesRendered =
    messages.length > 0
      ? messages.map(message => {
          return (
            <li key={message._id}>
              <div className="message-header">
                <div>
                  <h1 className="sender-info">
                    {message.name}
                    {!message.read ? (
                      <span className="read-circle"></span>
                    ) : null}
                  </h1>
                  <h3>{message.email}</h3>
                </div>
                <button
                  type="button"
                  onClick={() => markAsRead(message._id, message.read)}
                >
                  Mark as {message.read ? "Unread" : "Read"}
                </button>
              </div>
              <h2>{message.subject}</h2>
              <p>{message.text}</p>
            </li>
          );
        })
      : null;
  const handleSubmit = async e => {
    e.preventDefault();
    setError({
      error: false,
      message: ""
    });
    try {
      const response = await axios.post("/api/admin", { email, pass });
      window.localStorage.setItem("token", JSON.stringify(response.data));
      setLoggedAdmin(response.data);
    } catch (err) {
      setError({
        error: true,
        message: "Wrong Email or Password."
      });
    }
  };
  useEffect(() => {
    if (loggedAdmin) {
      axios
        .get("/api/admin/messages", {
          headers: { authorization: `bearer ${loggedAdmin.token}` }
        })
        .then(response => {
          setMessages(response.data);
        })
        .catch(err => {
          setError({
            error: true,
            message: "Admin Not logged in.Please log in again."
          });
        });
    }
  }, [loggedAdmin]);

  return (
    <main className="admin-main">
      {!loggedAdmin ? (
        <div className="form-container-container">
          <div className="form-container">
            <h2>Login</h2>
            {error.error && <p style={{ color: "red" }}>{error.message}</p>}
            <form onSubmit={handleSubmit}>
              <input
                type="text"
                value={email}
                onChange={e => setEmail(e.target.value)}
                placeholder="Email"
              />
              <input
                type="password"
                value={pass}
                onChange={e => setPass(e.target.value)}
                placeholder="Password"
              />
              <button type="submit">Login</button>
            </form>
          </div>
        </div>
      ) : (
        <div>
          {error.error && <p style={{ color: "red" }}>{error.message}</p>}
          <div className="admin-board">
            <div className="admin-header">
              <h2>Hi Nasser</h2>
              <h4>
                You have new{" "}
                {messages.filter(message => message.read === false).length}{" "}
                Messages
              </h4>
              <button
                type="button"
                onClick={() => {
                  window.localStorage.clear();
                  setLoggedAdmin(false);
                }}
              >
                Log Out
              </button>
            </div>
            <hr />
            <div className="messages-box">
              <ul>{messagesRendered}</ul>
            </div>
          </div>
        </div>
      )}
    </main>
  );
};

export default Admin;
